<template>
  <div class="box">
    <van-nav-bar
      left-arrow
      @click-left='$router.go(-1)'
      title="点赞"
      :border="false"
      fixed
    />
    <section>
      <div class="b">
        <van-tabs v-model="activeName" title-active-color='red' :animated='true' line-height='0'>
          <van-tab title="通知" name="a">
            <div :activeName='activeName' class="message" v-if="informList.length">
              <div class="message_list" v-for="(item, index) in informList" :key="index">
                <div class="message_list_user">
                  <img :src="item.message_uservia" alt="">
                  <p>{{item.message_nickname}}</p>
                  <p>{{item.message_timer}}</p>
                </div>
                <div class="message_list_text" v-html="item.message_content"></div>
                <div class="message_list_btn">
                  <span @click="goInformDetail(item.message_id)">查看更多</span>
                </div>
              </div>
            </div>
            <div v-else class="noData">
              <img src="../../assets/liwudi.png" alt="">
              <span>您还没有相关的点赞~</span>
            </div>
          </van-tab>
          <van-tab title="日记" name="b">
            <div :activeName='activeName' class="diary" v-if="diaryList.length">
              <div class="diary_list" v-for="(item, index) in diaryList" :key="index" @click="goDiaryDetail(item.diary_id)">
                <img :src="item.diary_issue_pic" alt="">
                <div class="diary_list_text">
                  <p>{{item.diary_issue_content}}</p>
                  <p>
                    <span>{{item.diary_issue_nickname}}</span>
                    <span>{{'阅读量' + item.diary_recommend_viewsNumber}}</span>
                  </p>
                </div>
              </div>
            </div>
            <div v-else class="noData">
              <img src="../../assets/liwudi.png" alt="">
              <span>您还没有相关的点赞~</span>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </section>
  </div>
</template>

<script>
import { getUserLike } from '../../api'
import Vue from 'vue'
import { NavBar, PullRefresh, Icon, Tab, Tabs } from 'vant'
Vue.use(NavBar)
Vue.use(PullRefresh)
Vue.use(Icon)
Vue.use(Tab)
Vue.use(Tabs)
export default {
  data () {
    return {
      activeName: 'a',
      informList: [],
      diaryList: []
    }
  },
  mounted () {
    this.getUserLikeDiaryList()
    this.getUserLikeMessageList()
  },
  methods: {
    getUserLikeMessageList () {
      getUserLike({
        username: localStorage.getItem('userTel_AM'),
        type: '通知'
      }).then(res => {
        if (res.data.code === '200') {
          this.informList = res.data.data
        }
      })
    },
    getUserLikeDiaryList () {
      getUserLike({
        username: localStorage.getItem('userTel_AM'),
        type: '日记'
      }).then(res => {
        // console.log(res.data.data)
        if (res.data.code === '200') {
          this.diaryList = res.data.data
          // console.log(this.diaryList)
        }
      })
    },
    goInformDetail (messageId) {
      this.$router.push('/inform/informDetail?message_id=' + messageId)
    },
    goDiaryDetail (diaryId) {
      const diaryid = diaryId.toString()
      this.$router.push({ path: '/Redetail/' + 'diary_id=' + diaryid })
    }
  }
}
</script>

