      }
      p:nth-of-type(1) {
        color: #333333;
        font-size: 0.12rem;
      }
      p:nth-of-type(2) {
        color: #999999;
        font-size: 0.1rem;
      }
    }
    .message_list_text {
      width: 100%;
      height: 0.48rem;
